.files-wrapper { margin: 0 -10px;}
.file-item { font-weight: normal; }
.file-item .inner { border: $global-border; padding: 0; background: #FFF; }
.file-item .image-wrapper { padding-bottom: 66.6%; height: 0; position: relative; border: $global-border; background: #1B1B28; }
.file-item .image-wrapper img { position: absolute; top: 50%; left: 50%; object-fit: contain; transform: translate(-50%,-50%); width: 100%; height: 100%; }
.file-item .copy-wrapper { padding: $global-padding; position: relative; }

.remove-file { position: absolute; top: 0; right: 0; }



.file-item .icon-wrapper { padding-bottom: 66.6%; height: 0; position: relative; border: $global-border; background: #1B1B28; }
.file-item .icon-wrapper .fa {}
.file-item .icon-wrapper .fa {   color: #FFF; font-size: 80px; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }


.progress-wrapper { padding-bottom: 66.6%; height: 0; position: relative; border: $global-border; background: #1B1B28; }

.progress-circle { background-color: #ddd; border-radius: 50%; display: inline-block; height: 60%; margin: 20px; position: relative; width: 40%; width: 40%; position: absolute; margin: 0; top: 50%; left: 50%; transform: translate(-50%,-50%); }
.progress-circle:before { align-items: center; background-color: #fff; border-radius: 50%; content: attr(data-progress) '%'; display: flex; font-size: 3rem; justify-content: center; position: absolute; left: 1rem; right: 1rem; top: 1rem; bottom: 1rem; transition: -webkit-transform 0.2s ease; transition: transform 0.2s ease; transition: transform 0.2s ease, -webkit-transform 0.2s ease; }
.progress-circle:after { background-color: #0083ff; border-radius: 50%; content: ''; display: inline-block; height: 100%; width: 100%; }
.progress-circle:hover:before,
.progress-circle:focus:before { -webkit-transform: scale(0.8); transform: scale(0.8); }
.progress-circle[data-progress="0"]:after { background-image: linear-gradient(90deg, #ddd 50%, transparent 50%, transparent), linear-gradient(90deg, #0083ff 50%, #ddd 50%, #ddd); }
